html {
  text-align: center;
}
.flow_pc {
  width: 100%;
  /* height: 2000px; */
  min-width: 350px;
  margin: auto;
  padding-top: 30px;
  padding-bottom: 30px;
  margin-top: 20px;
  background-color: #141414;
  border-radius: 60px 60px 15px 15px;
  box-shadow: 0px -3px 16px #000;
}
.flow_title_pc {
  font-family: vdl-logojrblack, sans-serif;
  font-weight: 900;
  font-style: normal;
  font-size: 6rem;
  border-bottom: solid 2px #fff;
  width: 75%;
  margin: auto;
  padding-bottom: 5px;
}
.flow_title_sub_pc {
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 3rem;
  padding-top: 5px;
}
.flow_sentence_content_pc {
  width: 75%;
  margin: 30px auto;
}
.flow_sentence_pc {
  width: 100%;
  margin: 80px auto;
}
.flow_sentence_pc p {
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
  text-align: left;
  line-height: 1.4;
  font-size: 1.8rem;
}
.flow_sentence_title_pc {
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 4rem;
  margin-bottom: 30px;
  text-align: left;
  color: #10a172;
  /* border-bottom: solid 2px #fff; */
}
.flow_sentence_content_pc p:last-child {
  margin-bottom: 50px;
}
