.weapon {
  width: 100%;
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
  /* height: 2000px; */
  min-width: 350px;
  margin: auto;
  padding-top: 30px;
  padding-bottom: 30px;
  margin-top: 20px;
  background-color: #141414;
  border-radius: 60px 60px 15px 15px;
  box-shadow: 0px -3px 16px #000;
}
.weapon_title {
  font-size: 3rem;
  width: 50%;
  margin: auto;
  margin-bottom: 30px;
}
.weapon_wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.weapon_content {
  width: 100%;
  font-size: 2rem;
  background-color: #1c1c1c;
  padding-top: 15px;
  margin-bottom: 30px;
  border-radius: 16px;
}
.weapon_content2 {
  width: 45%;
}
.weapon_name {
  margin-bottom: 30px;
}
.weapon_img {
  width: 40%;
  margin-bottom: 15px;
}
.weapon_type {
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1.8rem;
  margin-bottom: 10px;
}
.weapon_kind {
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1.8rem;
  margin-bottom: 20px;
}
.red {
  color: #c91907;
}
.blue {
  color: #8343f4;
}
.green {
  color: #31c906;
}
