.wrapper_pc {
  position: relative;
}
.drawer_img_pc {
  width: 60%;
  position: absolute;
  left: 85%;
  bottom: 0;
}
.drawer_title_pc {
  font-family: vdl-logojrblack, sans-serif;
  font-weight: 900;
  font-style: normal;
  font-size: 6rem;
  padding-left: 3%;
  padding-top: 10px;
}
.drawer_nav_pc {
  width: 70%;
  display: flex;
  justify-content: space-around;
  text-align: center;
  font-size: 4rem;
  margin-top: 20px;
  padding-right: 3%;
  padding-top: 50px;
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
}
.drawer_nav_pc li {
  margin-bottom: 20px;
  padding-bottom: 3px;
  border-bottom: solid 2px #000;
}
.weaponDrawer_pc {
  font-size: 4rem;
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
}
.weaponDrawer_title_pc {
  font-size: 6rem;
  font-family: vdl-logojrblack, sans-serif;
  font-weight: 900;
  font-style: normal;
  margin-top: 80px;
  margin-bottom: 60px;
}
.weaponDrawer_ul_pc {
  margin-bottom: 80px;
}
.weaponDrawer_ul_pc li {
  margin-bottom: 8px;
}
.weaponDrawer_ul_title_pc {
  border-bottom: solid 3px;
  width: 80%;
  margin: auto;
  margin-bottom: 5px;
  padding-bottom: 5px;
}
