.wrapper {
  position: relative;
}
.drawer_img {
  width: 75%;
  position: absolute;
  left: 5%;
  bottom: 0;
}
.drawer_title {
  font-family: vdl-logojrblack, sans-serif;
  font-weight: 900;
  font-style: normal;
  font-size: 4rem;
  padding-left: 3%;
  padding-top: 10px;
}
.drawer_nav {
  text-align: right;
  font-size: 2rem;
  margin-top: 20px;
  padding-right: 3%;
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
}
.drawer_nav li {
  margin-bottom: 20px;
  padding-bottom: 3px;
  border-bottom: solid 2px #000;
}
.weaponDrawer {
  font-size: 2rem;
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
}
.weaponDrawer_title {
  font-size: 3rem;
  font-family: vdl-logojrblack, sans-serif;
  font-weight: 900;
  font-style: normal;
  margin-top: 80px;
  margin-bottom: 60px;
}
.weaponDrawer_ul {
  margin-bottom: 30px;
}
.weaponDrawer_ul li {
  margin-bottom: 8px;
}
.weaponDrawer_ul_title {
  border-bottom: solid 2px;
  width: 80%;
  margin: auto;
  margin-bottom: 5px;
  padding-bottom: 5px;
}
