.map_pc {
  width: 100%;
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
  /* height: 2000px; */
  min-width: 350px;
  margin: auto;
  padding-top: 30px;
  padding-bottom: 30px;
  margin-top: 20px;
  background-color: #141414;
  border-radius: 60px 60px 15px 15px;
  box-shadow: 0px -3px 16px #000;
}
.map_title_pc {
  font-family: vdl-logojrblack, sans-serif;
  font-weight: 900;
  font-style: normal;
  font-size: 7rem;
  width: 50%;
  margin: auto;
  margin-bottom: 30px;
}
.map_content {
  width: 90%;
  margin: auto;
  margin-bottom: 40px;
  font-size: 2rem;
  border: solid 4px #10a172;
  border-radius: 20px;
  padding: 20px;
}
.map_img {
  width: 100%;
  max-width: 100%;
  margin-bottom: 15px;
}
.map_name {
  font-size: 2.5rem;
  padding-bottom: 5px;
  border-bottom: solid 2px #10a172;
  margin-bottom: 20px;
}
.map_content p {
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
  margin-bottom: 15px;
}
.map_content p:last-child {
  margin-bottom: 5px;
}
.map_button {
  font-size: 2.4rem;
  width: 40%;
  margin: auto;
  background-color: #10a172;
  padding: 10px;
  border-radius: 15px;
  box-shadow: 0 7px #0b6e4e;
}
.map_drawer {
  background-color: #fff;
  width: 100%;
}
