html {
  width: 100%;
  box-sizing: border-box;
  font-size: 62.5%;
  background-color: #1d1d1d;
  color: #fff;
}
p {
  font-family: "游ゴシック体", "游ゴシック";
}

/* --ページヘッダー-- */
