html {
  width: 100%;
  box-sizing: border-box;
  font-size: 62.5%;
  background-color: #1d1d1d;
  color: #fff;
}
p {
  font-family: "游ゴシック体", "游ゴシック";
}

/* ----スマホCSS---- */
/* --ページヘッダー-- */
.page_sp {
  text-align: center;
}
.title {
  font-family: vdl-logojrblack, sans-serif;
  font-weight: 900;
  font-style: normal;
}
.sub_title {
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
}
.page_title_sp {
  font-size: 2.8rem;
  width: 100%;
  min-width: 350px;
  height: 95px;
  padding-top: 60px;
  color: #10a172;
  background-image: url("https://drive.google.com/uc?export=view&id=1rsaOKPY0BZN8w0uJ7V6ulDD5SMb5HtVh");
  background-size: cover;
  position: relative;
  top: 0;
  left: 0;
  z-index: 999;
}
.page_title {
  font-size: 2.8rem;
  width: 100%;
  height: 60px;
  padding: 15px;
  background-color: #10a172;
  margin-bottom: 35px;
}
.page_nav_sp {
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1.5rem;
  background-color: #10a172;
  height: 35px;
  display: none;
}
.page_nav_ul_sp {
  display: flex;
  justify-content: space-between;
  width: 90%;
  margin: 0 auto;
}
/* --メインコンテンツ-- */
#top {
  width: 100%;
  margin: auto;
  text-align: center;
}
.page_top_sp {
  width: 100%;
  border-radius: 60px 60px 15px 15px;
  padding-top: 30px;
  padding-bottom: 50px;
  margin-top: 20px;
  background-color: #141414;
  box-shadow: 9px 8px 6px #0a0a0a;
}
.page_top_title_sp h2 {
  font-family: vdl-linegrpop-shadow, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 3rem;
}
.page_top_title_sp p {
  margin-top: 5px;
  font-size: 1.5rem;
}
.top_sentence {
  width: 80%;
  margin: auto;
  padding-top: 30px;
  margin-bottom: 20px;
  line-height: 1.45;
  font-size: 1.5rem;
  text-align: left;
}
