html {
  text-align: center;
}
.flow {
  width: 100%;
  /* height: 2000px; */
  min-width: 350px;
  margin: auto;
  padding-top: 30px;
  padding-bottom: 30px;
  margin-top: 20px;
  background-color: #141414;
  border-radius: 60px 60px 15px 15px;
  box-shadow: 0px -3px 16px #000;
}
.flow_title {
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 3rem;
  border-bottom: solid 2px #fff;
  width: 50%;
  margin: auto;
  padding-bottom: 5px;
}
.flow_title_sub {
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1.5rem;
  padding-top: 5px;
}
.flow_sentence {
  width: 90%;
  margin: 30px auto;
}
.flow_sentence p {
  text-align: left;
  line-height: 1.5;
  font-size: 1.5rem;
}
.flow_sentence_title {
  font-family: vdl-logog, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 2rem;
  margin-bottom: 10px;
  /* border-bottom: solid 2px #fff; */
}
.flow_sentence_content p:last-child {
  margin-bottom: 50px;
}
